.cheat{
    position: absolute;
    margin-left: min(43vw,43vh);
    margin-top: min(60vw,60vh);
    width: 100px;

}

.player-board{
    /* background-color: rgba(0, 0, 0,.8); */
}
.player-board{
    width: min(50vw, 50vh);
    height: min(50vw, 50vh);
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: repeat(10, 1fr);
    /* margin-bottom: 30px; */
    /* z-index: -1; */
}
.space{
    border: 3px solid #588ac1;
    border-radius: 5px;
    width: min(5vw, 5vh);
    height: min(5vw, 5vh);
    /* background-color: rgb(186, 181, 181); */

}

.player-area{
    display: grid;
    grid-template-columns: 5fr 10fr ;
    grid-template-rows: 1fr, 10fr;
}

.control{
    grid-column: 1;
    grid-row: 1;
    width: 25vw;
}

.loading-dock {
    width: min(30vw, 30vh);
    height: min(50vw, 50vh);
    grid-column: 1;
    display: flex;
    flex-wrap: wrap;
    flex-grow: 0;
    align-items: center;
    gap: 5px;
    /* display: grid;
    grid-template-columns:repeat(5, 1fr);
    grid-template-rows: repeat(10,1fr); */
    border: 2px solid rgba(163, 162, 162,.8);
}

.status-container{
    margin-left: min(1vw,1vh);
    margin-top: min(-5vw,-5vh);
    display: grid;
    grid-template-rows: 2;
    justify-content: center;
    gap: min(10vw,10vh);
   
}
.title{
    font-size: min(4vw,4vh);
}

.space.enemy{
    border: 3px solid rgba(244, 48, 48, 0.8);
}

.space.air-carrier{
    background-color: #494546;
}

.carrier{
    border: 3px solid #696e6a;
    border-radius: 15px;
    background-color: #696e6a;
}

.carrier>div:first-child{
    background-color: #0077cc;
    border-radius: 75%;
}
.space.battleship{
    background-color: #c2c5c3;
}

.space.cruiser{
   background-color: #9ea29f;
}

.space.destroyer{
    background-color: #7a7f7b;
